const $lastLi = $('.siteList').find('li.last')
const $addSite = $('.addSite')
const x = localStorage.getItem('x')//ÊèêÂèñÊï∞ÊçÆ
const xObject = JSON.parse(x)
const hashMap = xObject || [
    { logo: "I", url: "https://www.iconfont.cn" },
    { logo: "F", url: "https://figma.com" },
]
const simplifyUrl = url => {//ÁÆÄÂåñURL
    return url.replace('https://', '')
        .replace('http://', '')
        .replace('www.', '')
        .replace(/\/.*/, '')//‰ΩøÁî®Ê≠£ÂàôË°®ËææÊâæÂà∞"/"Âèä"/"‰πãÂêéÁöÑÂÜÖÂÆπ
}
const render = () => {
    $('.siteList').find('li:not(.last)').remove()
    hashMap.forEach((item, index) => {
        const $li = $(`<li>
                <div class="site">
                    <div class="logo">${item.logo}</div>
                    <div class="link">${simplifyUrl(item.url)}</div>
                    <div class="close">
                        <svg class="icon" >
                            <use xlink:href="#icon-close"></use>
                        </svg>
                    </div>
                </div>
        </li >`).insertBefore($lastLi)//ÈÅçÂéÜÂìàÂ∏åË°®ÔºåÊ∑ªÂä†ÁΩëÂùÄÂà∞lastLi‰πãÂâç
        $li.on('click', () => { window.open(item.url) })
        $li.on('click', '.close', (e) => {
            e.stopPropagation()//ÈòªÊ≠¢ÂÜíÊ≥°
            hashMap.splice(index, 1)
            render()//ÈáçÊñ∞Ê∏≤ÊüìÂìàÂ∏åË°®
        })
    })
}
render()

$addSite.on('click', () => {
    let url = window.prompt('ËØ∑ËæìÂÖ•Ë¶ÅÊ∑ªÂä†ÁöÑÁΩëÂùÄÔºÅÈªòËÆ§ÊòØhttps://')//Ê∑ªÂä†ÊèêÁ§∫Á™óÂè£
    if (url === null) { return }
    if (url.indexOf('http') !== 0) {
        url = "https://" + url
    }
    hashMap.push({ logo: simplifyUrl(url)[0].toUpperCase(), url: url })
    render()
})

window.onbeforeunload = () => {
    const string = JSON.stringify(hashMap)
    window.localStorage.setItem('x', string)//Â≠òÂÇ®Êï∞ÊçÆ
}
